import './App.css';
import {useState} from "react";
import axios from "axios";
import {create, get} from "@github/webauthn-json";

function App() {
  const [username, setUsername] = useState("");
  const [res, setRes] = useState("");

  const register = async () => {
    try {
      const {data} = await axios.post(`/registration/start?username=${username}`)

      const credential = await create({publicKey: data.creationOptions});

      const payload = {
        registrationId: data.registrationId,
        credential
      };

      axios.post('/registration/finish', payload)
        .then((response) => setRes(response.data))
        .catch(e => console.log(e));
    } catch (e) {
      if (e.response && e.response.data) setRes(e.response.data);
      else setRes(e);
    }
  }

  const login = async () => {
    try {
      const {data} = await axios.post(`/assertion/start?username=${username}`)

      const credential = await get({publicKey: data.credentialOptions});

      const payload = {
        assertionId: data.assertionId,
        credential
      };

      axios.post('/assertion/finish', payload)
        .then((response) => setRes(response.data))
        .catch(e => console.log(e));
    } catch (e) {
      console.log(e);
      if (e.response && e.response.data) setRes(e.response.data);
      else setRes(e);
    }
  }

  return (
    <div className="App" style={{padding: '1rem'}}>
      <input value={username} onChange={e => setUsername(e.target.value)}/>
      <button onClick={register}>Register</button>
      <button onClick={login}>Login</button>
      {res && (<div>{JSON.stringify(res)}</div>)}
    </div>
  );
}

export default App;
